/* ================================= 
  ELEMENT STYLES
====================================  */

{
  box-sizing: border-box;
}
body {
  color: #576366;
  background: #303030;
  margin: 0;
  font-family: 'Roboto', sans-serif;
 }
h1,
h2,
ul {
  margin: 0;
  padding: 0;
}
h2 {
  color: #404040;
  font-size: 1.6em;
  margin-bottom: 1.25em;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #3b80d1;
}
hr {
  border: 0;
  height: 0;
  margin: 2.3em 0 2em 0;
  border-top: 1px solid rgba(0, 0, 0, .1);
}

/* FORM & BUTTON STYLES -------- */

 .home a,
input,
button {
  padding: .5em;
  font-size: .9em;
  margin-right: 10px;
  border: none;
  border-radius: 3px;
  border: solid 1px #ccc;
  outline: none;
}
button {
  color: white;
  background: #4188dc;
  padding-left: 1em;
  padding-right: 1em;
  cursor: pointer;
  border-color: rgba(0,0,0, .1);
} 

/* ================================= 
  LAYOUT & PAGE STYLES
==================================== */

.container {
  background: #fff;
  width: 100%;
  max-width: 1280px;
  padding-bottom: 2em;
  padding-left:0;
  padding-right:0;
  margin: 0 auto 2em;
  border-radius: .5em;
  box-shadow: 0 3px rgba(0,0,0, .1);
  overflow: hidden; 
}
header {
  background: #000000;
  margin-bottom: 2em;
  height:60.8px;
  width: 100%;
}

.main-content {
  padding: 1em 2em;
}
.group:after {
  content: "";
  display: table;
  clear: both;
}
.course {
  margin-bottom: 1em;
} 

/* IMAGES -------- */

.course-img {
  width: 70px;
}
.teacher-img {
  width: 100%;
  border-radius: .3em;
}
.not-found {
  text-align: center;
}

img {
  max-width:100%;
  max-height: 100%;
  height:auto;
} 

/* MAIN NAV -------- */

.main-nav a {
  font-size: .95em;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  display: block;
  padding: 1em;
  -webkit-transition: .2s;
  transition: .2s;
}
.main-nav a:hover,
.main-nav .active {
  color: #fff;
  background: #3b80d1;
} 
.icn-logo {
  margin-right: auto;
  padding: 0 2em;
}
header,
.main-nav {
  display: flex;
  flex-wrap: wrap;
}
.main-nav a {
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  padding: 1.25em 1.5em;
}
.main-nav a:hover {
  border-color: rgba(0,0,0, .1);
} 

